import React, { createContext, useState } from 'react'
import { BodyLayout, Title } from './style'
import FunctionBar from '../FunctionBar'
import Text from '../Text'
import Recoder from './Recoder'
import originContext from './originContext'
import ControlArea from './ControlArea'
import TextBox from '../../atom/TextBox'

const funcBarProps = {
    "align-items": "center",
    "border-radius": "20px"
}

const buttonProps = {
    margin: "0 0 0 15px",
    padding: "15px",
    height: "60px",
    border: "none",
    cursor: "pointer",
    "border-radius": "35px",
    "&": "hover{color: blue;}"
}

const BeforeAttrs = [
    <Title><Text contentId="language" /></Title>
]

const AfterAttrs = [
    <ControlArea contentId="streaming" />,
    <Recoder />
]

export const BodyContext = createContext();

function Body() {
    const [context, setContext] = useState(originContext)
    const { recognizeResult } = context;

    return (
        <BodyContext.Provider value={{ context, setContext }}>
            <BodyLayout>
                <FunctionBar
                    width="80%"
                    buttonProps={buttonProps}
                    funcBarProps={funcBarProps}
                    BeforeAttrs={BeforeAttrs}
                    AfterAttrs={AfterAttrs}
                />
                {
                    recognizeResult ? <TextBox label="Result" value={recognizeResult}/> : <Text contentId={"usage"} />
                }
            </BodyLayout>
        </BodyContext.Provider>
    )
}

export default Body