import React, { useCallback, useContext, useRef } from 'react'
import Section from "../../atom/Section";
import { Header, SectionLayout } from './style'
import lang from '../../lang.json'
import { LangContext } from '../../App';
import Image from '../../atom/Image';

function Nav() {
    const { langId, setLangId } = useContext(LangContext)
    const screenWidth = useRef(window.innerWidth)

    const handleChange = useCallback((e) => {
        setLangId(e.target.value);
    }, [setLangId])

    return (
        <Header>
            <Image src="logo.png" width={screenWidth.current < 768 ? "60%": "35%"} onClick={() => document.location = "https://wmmks.csie.ncku.edu.tw/"} />
            <SectionLayout>
                <Section sections={lang} onChange={handleChange} placeHolder={langId} />
            </SectionLayout>
        </Header>
    )
}

export default Nav