import React, { useCallback, useContext } from 'react'
import { StyledMicIcon, StyledSettingsVoiceIcon } from './style'
import { BodyContext } from '..'
import MicrophoneRecoder from '../../../utils/microphoneRecoder';
import postVoice from '../../../api/postVoice';

const record = new MicrophoneRecoder();

function Recoder() {
    const { context, setContext } = useContext(BodyContext);
    const { isRecording, isStreaming } = context

    const handleClick = useCallback(() => {
        if (isRecording) {
            record.stopRecording();
            const fn = (result) => {
                setContext({ ...context, recognizeResult: result, isRecording: !isRecording });
            }
            postVoice(fn);
        }
        else {
            record.startRecording();
            setContext({ ...context, isRecording: !isRecording })
        }
    }, [context, setContext, isRecording])

    return (
        <>
            {
                isStreaming ?
                    <StyledSettingsVoiceIcon active={isRecording} onClick={handleClick} /> :
                    <StyledMicIcon active={isRecording} onClick={handleClick} />
            }
        </>
    )
}

export default Recoder