import { useContext, useEffect, useState } from 'react'
import words from '../words.json'
import { LangContext } from '../App';
import getLSF from '../api/getLSF'

function useAutoLang(contentId = "") {
  const [text, setText] = useState("");
  const word = words[contentId] ? words[contentId] : ""
  const isExtraFile = word.startsWith("LSF:");
  const { langId } = useContext(LangContext);

  useEffect(() => {
    if (isExtraFile) {
      getLSF({ word, langId, setText })
    }
    else {
      setText(word.split('|')[langId]);
    }
  }, [word, isExtraFile, langId])

  return [text, isExtraFile]
}

export default useAutoLang