async function postVoice(fn){
    const audio = window.__audio;
    if (!audio) {
        setTimeout(()=>postVoice(fn), 50);
        return;
    }

    const formData = new FormData();
    formData.append('audio', audio)
    let res = await fetch("/api/regconization", {
        method: "POST",
        body: formData,
    })
    res = await res.text();
    console.log(fn)
    window.__audio = "";
    fn(res);
}
export default postVoice;