import React from 'react'
import { FunctionBarLayout } from './style'
import Button from '../../atom/Button'
import Text from '../Text'

function FunctionBar(
    { buttonProps,
        funcBarProps,
        contentIds = [],
        onClicks = [],
        BeforeAttrs = [],
        AfterAttrs = []
    }) {
        
    return (
        <FunctionBarLayout {...funcBarProps} >
            {BeforeAttrs}
            {
                contentIds.map((contentId, i) => {
                    return <Button key={i} {...buttonProps} onClick={onClicks.length ? onClicks[i] : ""}>
                        <Text key={i} contentId={contentId}></Text>
                    </Button>
                })
            }
            {AfterAttrs}
        </FunctionBarLayout>
    )
}

export default FunctionBar