import React, { useCallback, useContext } from 'react'
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import { ControlAreaLayout } from './style';
import useAutoLang from '../../../utils/useAutoLang';
import { BodyContext } from '..';

function ControlArea({ contentId }) {
    const [text,] = useAutoLang(contentId)
    const { context, setContext } = useContext(BodyContext)

    const handleChange = useCallback(() => {
        const nextContext = { ...context };
        nextContext.isStreaming = !nextContext.isStreaming;

        setContext(nextContext);
    }, [context, setContext])

    return (
        <ControlAreaLayout onChange={handleChange}>
            <FormControlLabel control={<Switch />} label={text} labelPlacement='start' />
        </ControlAreaLayout>
    )
}

export default ControlArea