import React from 'react'
import { Span } from './style'
import TextBox from '../../atom/TextBox';
import useAutoLang from '../../utils/useAutoLang';

function Text({ contentId, size, endl }) {
    const [text, isExtraFile] = useAutoLang(contentId)

    return (
        <>
            {
                text ? ((isExtraFile) ? <TextBox label="" value={text} />
                    : ((endl === true)
                        ?
                        <p><Span size={size}> {text} </Span></p>
                        :
                        <Span size={size}> {text} </Span>)) :
                    <></>
            }
        </>
    )
}

export default Text